import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back
    },
    data () {
        return {
            list:[],
            TypeId:this.$route.query.TypeId,
            num:0,
            list2:[],
        }
    },
    created() {
        this.getFirst()
        console.log(this.TypeId)
    },
    methods: {
         getFirst () {
            this.$get('AppointmentAPI/Appointment/GetAppointmentTypes',{
                ParentId: 0
            }).then( m => {
                this.list = m
                this.list.forEach((v,k) =>{
                    if(v.TypeId === Number(this.TypeId)){
                        this.num = k
                        this.getSecond(v,k)
                    }
                })
            })
        },
        getSecond (i,idx) {
            this.num = idx
            console.log(idx)
            this.$get('AppointmentAPI/Appointment/GetAppointmentTypes',{
                ParentId: i.TypeId
            }).then( m => {
                this.list2 = m
            })
        },
        goDepartment(i){
            this.$store.state.saveVipData.AppointmentTypeId = i.TypeId
            this.$store.state.saveVipData.Hospital = null
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
            this.$router.push('/vipArea')
        },
    },
}



